.popup__input {
  padding: 0;
  padding-bottom: 8px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
  color: #000000;
}

.popup__input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
  color: #000000;
}

.popup__input:focus {
  outline: none;
}
